.headerRow,
.evenRow,
.oddRow {
  border-bottom: 1px solid #e0e0e0;
}

.oddRow {
  background-color: #fafafa;
}

.headerColumn {
  text-transform: none;
}

.cboxContainer {
  display: flex;
  flex-direction: column;
  flex: 0 0 auto;
  min-width: 0;
  margin: 0 10px 5px 0;
  height: 62px;
}

.cboxContainer:last-of-type {
  margin-right: 0;
}

.cboxLabel {
  display: inline-block;
  width: 40%;
  box-sizing: border-box;
  color: #454545;
  font-size: .75em;
  margin-bottom: 5px;
  font-weight: 100;
}

ul.ksCboxtags {
    list-style: none;
    padding: 0px 0px;
    margin: 0px 0px; 
}
ul.ksCboxtags li{
  display: inline;
}
ul.ksCboxtags li label{
    display: inline-block;
    background-color: rgba(255, 255, 255, .9);
    border: 2px solid #adadad;
    color: #adadad;
    border-radius: 12px;
    white-space: nowrap;
    margin: 0px 3px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    transition: all .2s;
}

ul.ksCboxtags li label {
    padding: 2px 10px;
    cursor: pointer;
}

ul.ksCboxtags li label::before {
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    font-family: "Font Awesome 5 Free";
    padding: 1.5px 6px 1.5px 2px;
    transition: transform .3s ease-in-out;
    border: 2px solid #7986cb;
}

ul.ksCboxtags li input[type="checkbox"]:checked + label:hover {
    border: 2px solid #c5cae9;
    background-color: #c5cae9;
}


ul.ksCboxtags li input[type="checkbox"]:checked + label {
    background-color: #7986cb;
    border: 2px solid #7986cb;
    color: #fff;
}

ul.ksCboxtags li input[type="checkbox"] {
  display: absolute;
}
ul.ksCboxtags li input[type="checkbox"] {
  position: absolute;
  opacity: 0;
}


ul.ksCboxMW {
    list-style: none;
    padding: 0px 0px;
    margin: 0px 0px; 
}
ul.ksCboxMW li{
  display: inline;
}
ul.ksCboxMW li label{
    display: inline-block;
    background-color: rgba(255, 255, 255, .9);    
    color: #adadad;
    white-space: nowrap;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    transition: all .2s;
}

ul.ksCboxMW li label {
    padding: 3px 10px;
    cursor: pointer;
}

ul.ksCboxMW li label::before {
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    padding: 1.5px 6px 1.5px 2px;
    transition: transform .3s ease-in-out;
}

ul.ksCboxMW li input[type="checkbox"]:checked + label:hover {
    background-color: #c5cae9;
}

ul.ksCboxMW li input[type="checkbox"]:checked + label::before {
    transform: rotate(-360deg);
    transition: transform .3s ease-in-out;
}

ul.ksCboxMW li input[type="checkbox"]:checked + label {
    background-color: #7986cb;
    color: #fff;
    transition: all .1s;
}

ul.ksCboxMW li input[type="checkbox"] {
  display: absolute;
}
ul.ksCboxMW li input[type="checkbox"] {
  position: absolute;
  opacity: 0;
}
